body {
  background: -webkit-gradient(linear, left top, right top, from(#0e0e19), to(#1b1d31));
  background: linear-gradient(to right, #0e0e19, #1b1d31);
  color: #ffffff;
}

img {
  display: block;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  max-width: 1024px;
  padding: 0 15px;
}

button {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  padding: 8px 30px;
  border: 0;
  border-radius: 5px;
  background: -webkit-gradient(linear, left top, right top, from(#3e44ff), color-stop(#8b8cff), to(#3e44ff));
  background: linear-gradient(to right, #3e44ff, #8b8cff, #3e44ff);
  color: #ffffff;
  font-size: 15px;
  font-weight: 600;
  letter-spacing: 0.8;
  line-height: normal;
}

button:hover {
  cursor: pointer;
  -webkit-box-shadow: 0 2px 6px 0 #3e44ff;
          box-shadow: 0 2px 6px 0 #3e44ff;
}

.hero {
  background-image: -webkit-gradient(linear, left top, right top, from(rgba(47, 48, 58, 0.4)), to(rgba(47, 48, 58, 0.4))), url("../src/img/banner/photo_2022-06-16_16-59-51.jpg");
  background-image: linear-gradient(to right, rgba(47, 48, 58, 0.4), rgba(47, 48, 58, 0.4)), url("../src/img/banner/photo_2022-06-16_16-59-51.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 118px;
  padding-bottom: 118px;
}

.hero__img {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.hero__title {
  font-size: 36px;
}

.contacts__messages_icon {
  width: 25px;
  height: 25px;
  fill: #ffffff;
}
